#smoke {
  position: absolute;
  z-index: 3;
  width: 1px;
  left: 80%;
  top: 0%;
}

/* smoke balls */
#smoke span {
  display: block;
  position: absolute;
  bottom: -35px;
  left: 50%;
  margin-left: -20px;
  height: 0px;
  width: 0px;
  border: 35px solid #4b4b4b;
  border-radius: 35px;
  left: -14px;
  opacity: 0;
  transform: scale(0.2);
}

/* Smoke animation */
@keyframes smokeL {
  0% {
    transform: scale(0.2) translate(0, 0);
  }
  10% {
    opacity: 1;
    transform: scale(0.2) translate(0, -5px);
  }
  100% {
    opacity: 0;
    transform: scale(1) translate(-20px, -130px);
  }
}

@keyframes smokeR {
  0% {
    transform: scale(0.2) translate(0, 0);
  }
  10% {
    opacity: 1;
    transform: scale(0.2) translate(0, -5px);
  }
  100% {
    opacity: 0;
    transform: scale(1) translate(20px, -130px);
  }
}

#smoke .s0 {
  animation: smokeL 5s 0s infinite;
}
#smoke .s1 {
  animation: smokeR 5s 1s infinite;
}
#smoke .s2 {
  animation: smokeL 5s 2s infinite;
}
#smoke .s3 {
  animation: smokeR 5s 3s infinite;
}
#smoke .s4 {
  animation: smokeL 5s 4s infinite;
}
#smoke .s5 {
  animation: smokeR 5s 5s infinite;
}
#smoke .s6 {
  animation: smokeL 5s 6s infinite;
}
#smoke .s7 {
  animation: smokeR 5s 5s infinite;
}
#smoke .s8 {
  animation: smokeL 5s 8s infinite;
}
#smoke .s9 {
  animation: smokeR 5s 9s infinite;
}
